<template >
    <div>
      <!-- 搜索 -->
            <div style="color:#fff">
      <van-nav-bar
        style="background-color:#00A77C;"
        title="硅谷外卖"
        left-text="返回"
        left-arrow
        @click-left="onClickLeft"
      />
    </div>
   
  <van-search
    v-model="value"
    show-action
    placeholder="请输入搜索关键词"
    @search="onSearch"
    @cancel="onCancel"
    
  >
  </van-search>

   <van-empty image="search" v-if="xinxi ==''" description="描述文字" />
<!-- 搜索后的数据 -->
 <!-- 信息 -->
 
    <div v-else style="padding: 10px;background:#fff;">
        <div  style="height:120px;border-bottom:1px solid  #e6e6e6 " v-for="(item,index) in xinxi"  :key="index" >
            <div style="float:left;width:100px;height:100px">
                <van-image 
  style="margin-top:7%"
  width="100%"
  height="100%"
   radius="10"
  fit="cover"
 :src='item.photo'
/>
                </div>
            <div style="float:right;width:65%;padding:10px;margin-top:2%" >
                <div style="font-size:17px;font-weight:550;">{{item.name}}</div>
                <div>           <span style="color:#FF7300;font-weight:600"> <span>{{item.score}}</span> 分</span><span class="a1" style="margin-left:10px">月售 <span>{{item.sales}}</span> </span></div>
            <div><span class="a1">起送:￥<span>{{item.minPrice}}</span></span> <span class="a1" style="margin-left:5px">配送:￥<span>{{item.transportationPrice}}</span></span></div>
            </div>
        </div>
    </div>
            <!-- 底部 -->
    <van-tabbar v-model="active">
      <van-tabbar-item :to="{
          name: 'shouye',
          params: { uId: uId, uName: uName },
        }" icon="home-o">商家</van-tabbar-item>
      <van-tabbar-item :to="{
          name: 'sousuo',
          params: { uId: uId, uName: uName },
        }"  icon="search">搜索</van-tabbar-item>
      <van-tabbar-item  :to="{
          name: 'dingdan',
          params: { uId: uId, uName: uName },
        }" icon="friends-o">订单</van-tabbar-item>
      <van-tabbar-item :to="{
          name: 'wode',
          params: { uId: uId, uName: uName },
        }"  icon="setting-o">我的</van-tabbar-item>
    </van-tabbar>
    </div>
</template>

<script>
import { Toast } from "vant";
import { Tabbar, TabbarItem } from 'vant';
import { Empty } from 'vant';
import { Search } from 'vant';
export default {
    props: ["uId", "uName"],
data() {
    return {
        active: 1,
         value: '',
         xinxi:[],
    };
  },
    methods: {
    onClickLeft() {
     this.$router.go(-1);
    },
    onSearch() {
     var app=this;
     app.$http.get("/biz/queryAllShopsInfoByName?name="+app.value).then(function(res){
       app.xinxi = res.data;
      for(var i=0;i<res.data.length;i++){
        // console.log(res.data[i])
        // console.log(res.data[i].name)
         app.xinxi[i].photo=
 "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/"+
  res.data[i].photo;
        
      }
            // console.log(res.data.length)
     })
    },
     onCancel() {
      Toast('取消');
    },
  }
}
</script>

<style>
body{
  background: #f8f8f8;
}
</style>



