<template>
  <div>
    <!-- 头部 -->
    <div style="color:#fff">
      <van-nav-bar
        style="background-color:#00A77C;"
        title="硅谷外卖"
        left-text="返回"
        left-arrow
        @click-left="onClickLeft"
      />
    </div>
    <van-grid>
      <van-grid-item
        v-for="(item,index) in good"
        :key="index"
        :icon="item.photo"
        :text="item.name"
        @click="getGod(item.id)"
      />
    </van-grid>
    <!-- 中间 -->
    <div style="height:40px;background-color:#F5F5F5;line-height:40px;padding:0 16px">
      <svg
        t="1621069230180"
        class="icon"
        viewBox="0 0 1024 1024"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        p-id="1881"
        width="16"
        height="16"
      >
        <path
          d="M163.2 208h64v64h-64zM336 208h524.8v64H336zM163.2 569.6h64v64h-64zM336 569.6h524.8v64H336zM163.2 390.4h64v64h-64zM336 390.4h524.8v64H336zM163.2 752h64v64h-64zM336 752h524.8v64H336z"
          fill="#707070"
          p-id="1882"
        ></path>
      </svg>
      <span @click="getXinxi()" style=" border-bottom: 2px solid #5757FF">附近商家</span>
    </div>

    <div style="padding: 10px">
      <div
        style="height:120px"
        @click="shangpin(item.id)"
        v-for="(item,index) in xinxi"
        :key="index"
      >
        <div style="float:left;width:100px;height:100px">
          <van-image width="100%" height="100%" radius="10" fit="cover" :src="item.photo"/>
        </div>
        <div style="float:right;width:65%;padding:10px">
          <div style="font-size:17px;font-weight:550">{{item.name}}</div>
          <div>
            <span style="color:#FF7300;font-weight:600">
              <span>{{item.score}}</span> 分
            </span>
            <span class="a1" style="margin-left:10px">
              月售
              <span>{{item.sales}}</span>
            </span>
            <span style="float:right">
              <span class="a1">
                {{item.deliveryTime}}
                <span>分钟</span>
              </span>
              <span class="a1">{{item.distance}}m</span>
            </span>
          </div>
          <div>
            <span class="a1">
              起送:￥
              <span>{{item.minPrice}}</span>
            </span>
            <span class="a1" style="margin-left:5px">
              配送:￥
              <span>{{item.transportationPrice}}</span>
            </span>
          </div>
          <div id="a1-1">"{{item.notice}}"</div>
        </div>
      </div>
    </div>

    <!-- 底部 -->
    <van-tabbar v-model="active">
      <van-tabbar-item
        :to="{
          name: 'shouye',
          params: { uId: uId, uName: uName },
        }"
        icon="home-o"
      >商家</van-tabbar-item>
      <van-tabbar-item
        :to="{
          name: 'sousuo',
          params: { uId: uId, uName: uName },
        }"
        icon="search"
      >搜索</van-tabbar-item>
      <van-tabbar-item
        :to="{
          name: 'dingdan',
          params: { uId: uId, uName: uName },
        }"
        icon="friends-o"
      >订单</van-tabbar-item>
      <van-tabbar-item
        :to="{
          name: 'wode',
          params: { uId: uId, uName: uName },
        }"
        icon="setting-o"
      >我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { Toast } from "vant";
import { Image as VanImage } from "vant";
import { Tabbar, TabbarItem } from "vant";

export default {
  name: "shouye",
  props: ["id", "uId", "uName"],
  data() {
    return {
      active: 0,
      good: [],
      error: "",
      xinxi: []
    };
  },

  created: function() {
    this.getGood();
    this.getXinxi();
    this.getGod();
  },
  methods: {
    onClickLeft() {
      this.$router.push("/");
    },
    shangpin(id) {
      this.$router.push({
        name: "shangpin",
        params: { shopid: id, uId: this.uId, uName: this.uName }
      });
    },
    //九宫格
    getGood: function() {
      var app = this;
      this.$http.get("/biz/queryBigCategory").then(function(res) {
        app.good = res.data;
        for (var i = 0; i < res.data.length; i++) {
          app.good[i].photo =
            "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/" +
            res.data[i].photo;
        }
      });
    },
    getXinxi: function() {
      if (this.id == undefined) {
        var app = this;
        this.$http.get("/biz/queryAllShopsInfo").then(function(res) {
          app.xinxi = res.data;
          for (var i = 0; i < res.data.length; i++) {
            app.xinxi[i].photo =
              "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/" +
              res.data[i].photo;
          }
        });
      } else {
        var app = this;
        this.$http
          .get("/biz/queryShopsByBigId?bigcategoryId=" + this.id)
          .then(function(res) {
            console.log(res);
            app.xinxi = res.data;
            for (var i = 0; i < res.data.length; i++) {
              app.xinxi[i].photo =
                "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/" +
                res.data[i].photo;
            }
          });
      }
    },
    getGod: function(id) {
      console.log(id);
      var app = this;
      this.$http
        .get("/biz/queryShopsByBigId?bigcategoryId=" + id)
        .then(function(res) {
          if (id < 16) {
            app.error = "";
            app.xinxi = res.data;
            for (var i = 0; i < res.data.length; i++) {
              app.xinxi[i].photo =
                "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/" +
                res.data[i].photo;
            }
          } else {
            app.error = "请求失败";
          }
        });
    }
  }
};
</script>

<style>
#a1-2 {
  border-radius: 20%;
  width: 100%;
  height: 100%;
  display: block;
}
.a1 {
  font-size: 5px;
}
#a1-1 {
  font-size: 10px;
  max-width: 60%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #f46308;
  background: #fef0e5;
}
</style>






