<template>
  <div style="width:300px;margin-left:10%">
    <!-- 头部 -->
    <div id="head">硅谷外卖</div>
    <!-- 切换 -->
    <div style="width:300px;background-color:#fff">
      <van-tabs>
        <van-tab title="登录">
          <van-form @submit="donSubmit">
             <div >
            <div style="margin-top:3%">
              <van-field
                v-model="dusername"
                name="duser"
                placeholder="用户名"
                :rules="[{ required: true, message: '请填写用户名' }]"
                clearable
              />
            </div>
            <div style="margin-top:3%">
              <van-field
                v-model="dpassword"
                :type="showornot"
                name="dpass"
                placeholder="密码"
                :rules="[{ required: true, message: '请填写密码'  }]"
                clearable
              >
                <template #button>
                  <span @click="isShow">
                    <van-icon name="closed-eye" v-if="show"/>
                    <van-icon name="eye-o" v-else/>
                  </span>
                </template>
              </van-field>
            </div>

            <div style="margin-top:3%">
              <van-field
                style="width:60%;float:left;"
                v-model="dverifycode"
                type="verifycode"
                name="dver"
                placeholder="请输入验证码"
                :rules="[{ required: true, message: '请输入验证码' }]"
                clearable
              ></van-field>
              <div @click="changeCode()" style="float:right">
                <!-- 引入验证码组件 -->
                <identify :identifyCode="identifyCode"></identify>
              </div>
            </div>
      

            <div>
              <van-button
                round
                block
                type="info"
                style="border-radius:5px;border-color:#77D57B;background-color:#77D57B;"
                native-type="dsubmit"
              >登录</van-button>
            </div>
                  </div>

          </van-form>
        </van-tab>

        <van-tab title="注册">
          <van-form @submit="zonSubmit">
            <van-field
              v-model="zusername"
              name="zuser"
              placeholder="用户名"
              :rules="[{ required: true, message: '请填写用户名' }]"
              clearable
            />

            <van-field
              v-model="zpassword"
              type="password"
              name="zpass"
              placeholder="密码"
              :rules="[{ required: true, message: '请填写密码' }]"
              clearable
            />
            <van-field
              v-model="zpassword1"
              type="password"
              name="zpass1"
              placeholder="重复密码"
              :rules="[{ required: true, message: '请再次填写密码' }]"
              clearable
            />

            <div style="margin: 16px;">
              <van-button
                round
                block
                type="info"
                style="border-radius:5px;border-color:#77D57B;background-color:#77D57B;"
                native-type="zsubmit"
              >提交</van-button>
            </div>
          </van-form>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import identify from "../components/identify.vue";
import { Toast } from 'vant';

export default {
  name: "denglu",
  
  components: { identify },
  data() {
    return {
      zusername: "",
      zpassword: "",
      zpassword1: "",
      dusername: "",
      dpassword: "",
      dverifycode: "",
      show: true,
      showornot: "password",
      // 验证码初始值
      identifyCode: "",

    };
  },
  mounted() {
    this.identifyCode = "";
    this.getCode();
  },

  methods: {
    // 验证码
    changeCode() {
      this.identifyCode = "";
      this.getCode();
    },
    getCode() {
      var app = this;
      app.$http.get("/user/sendCaptcha").then(function(res) {
        app.identifyCode = String(res.data);
      });
    },

    // 获取账号密码
    donSubmit() {
      if(this.dverifycode!=this.identifyCode){
        Toast('验证码错误');
        return false;
      }
      var app = this;
      app.$http.post("/user/login",{
          account: app.dusername,
          password: app.dpassword,
      }).then(function(res) {
        console.log(res);
        if(res.data==''){
          Toast('用户名或密码错误');
        }else{
          Toast('登录成功');

            app.$router.push({ name: 'wode', params: {uId: res.data.id, uName: res.data.name}})
          
        }
      }).catch(function (error) {
          console.log(error);
        });
    },
   
    //  账号判断
    isShow() {
      this.show = !this.show;
      if (this.show === true) {
        this.showornot = "password";
      } else {
        this.showornot = "text";
      }
    },



    // 注册账号密码
    zonSubmit() {
      if(this.zpassword!=this.zpassword1){
        Toast('两次密码不相同');
        return false;
      }
       var app = this;
      app.$http
        .post("/user/register", {
          account: app.zusername,
          password: app.zpassword,
          name: app.zusername,
        })
        .then(function (res) {
          console.log(res);
         if(res.data=="account is exit"){
           Toast('用户名已注册');
         }if(res.data=="register success"){
           Toast('注册成功');
         }
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    //--------------------------------
  },
};
</script>

<style>
#head {
  margin-top: 20%;
  font-size: 55px;
  font-weight: 700;
  color: #4aa478;
  text-align: center; 
}
</style>



