import Vue from 'vue'
import Router from 'vue-router'
import denglu from '@/components/denglu.vue';
import shouye from '@/components/shouye.vue';
import shangpin from '@/components/shangpin.vue';
import dingdan from '@/components/dingdan.vue';
import sousuo from '@/components/sousuo.vue';
import wode from '@/components/wode.vue';

Vue.use(Router)


export default new Router({
  
 routes:[
  { path: '/',name:'denglu', component: denglu },
  { path: '/dingdan',name:'dingdan', component: dingdan,props: true },
  { path: '/shouye',name:'shouye', component: shouye,props: true },
  { path: '/wode',name:'wode', component: wode,props: true},
  { path: '/sousuo',name:'sousuo', component: sousuo,props: true },
  { path: '/shangpin',name:'shangpin', component: shangpin,props: true },
]

})





