  <svg t="1621301818717" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1901" width="16" height="16"><path d="M372.363636 558.545455 907.636364 558.545455 791.272727 744.727273 884.363636 744.727273 1024 512 884.363636 279.272727 791.272727 279.272727 907.636364 465.454545 372.363636 465.454545Z" p-id="1902" fill="#97CBB2"></path><path d="M0 1024 744.727273 1024 744.727273 721.454545 651.636364 721.454545 651.636364 930.909091 93.090909 930.909091 93.090909 93.090909 651.636364 93.090909 651.636364 302.545455 744.727273 302.545455 744.727273 0 0 0Z" p-id="1903" fill="#97CBB2"></path></svg>
      注销登录</div>
   <van-icon class="w-c3" name="arrow"/>
</div>
    </div>

    <!-- 底部 -->
    <van-tabbar v-model="active">
      <van-tabbar-item :to="{
          name: 'shouye',
          params: { uId: uId, uName: uName },
        }" icon="home-o">商家</van-tabbar-item>
      <van-tabbar-item :to="{
          name: 'sousuo',
          params: { uId: uId, uName: uName },
        }"  icon="search">搜索</van-tabbar-item>
      <van-tabbar-item  :to="{
          name: 'dingdan',
          params: { uId: uId, uName: uName },
        }" icon="friends-o">订单</van-tabbar-item>
      <van-tabbar-item :to="{
          name: 'wode',
          params: { uId: uId, uName: uName },
        }"  icon="setting-o">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { Col, Row } from "vant";
import { Toast } from "vant";
import { Image as VanImage } from "vant";
import { Tabbar, TabbarItem } from "vant";
export default {
   props: ["uId", "uName"],
 
  data() {
    name
    return {
      active: 3
    };
  },
  methods: {
    onClickDen(){
       this.$router.push('/'); 
    },
    onClickLeft() {
      this.$router.go(-1);
    }
  }
};

</script>

<style>

#w-a1 {
  float: left;
  position: relative;
  left: 10px;
  top: 20px;
}
#w-a2 {
  float: left;
  position: relative;
  left: 30px;
  top: 30px;
  color: #fff;
}
#w-a3 {
  position: relative;
  margin-right: 5%;
  float: right;
  left: 10px;
  top: 40px;
  color: #fff;
}
.w-b1 {
  height: 80px;
  border-bottom: 1px solid #f1eff3;
}
.w-b2 {
  font-weight: 800;
  position: relative;
  top: 15px;
  text-align: center;
  font-size: 25px;
}
.w-b3 {
  font-size: 20px;
  color: #000;
}
.w-b4 {
  position: relative;
  top: 15px;
  text-align: center;
  font-size: 5px;
  color: #9b9c9f;
}
.w-c1{

  border-top: 1px solid #f1eff3;
  height:40px;
}
.w-c2{
    font-weight: 600;
    font-size: 15px;
  position: relative;
  top: 10px;
}
.w-c3{
   position: relative;
  top: 10px;
  float:right;
}
</style>



