<template>
  <div>
    <div style="color:#fff">
      <van-nav-bar
        style="background-color:#00A77C;"
        title="硅谷外卖"
        left-text="返回"
        left-arrow
        @click-left="onClickLeft"
      />
    </div>

    <!-- 未登录账号 -->
    <div v-if="uName==undefined">
      <van-empty>
        <van-button round type="danger" @click="denglu()" class="bottom-button">登录账号</van-button>
      </van-empty>
    </div>

    <!-- 已登录账号 -->
    <div v-else>
    <van-empty v-if="good.length==0" description="描述文字" />
      <div v-for="(item,index) in good" :key='index'>
        <van-card :thumb="item.photo">
          <template #tags>
            <div style="position: relative;top: 10px;">
              <div>
                <van-tag plain type="danger">
                  <span style="font-size:17px;font-weight:800;color:#000">{{item.shopName}}</span>
                  <span style="color:#000">
                    <van-icon name="arrow"/>
                  </span>
                </van-tag>
              </div>
              <div>
                <van-tag plain type="danger">
                  <span style="margin-top:20%;color:#000"> <span>{{item.list[0].foodName}}</span> 等1件商品</span>
                </van-tag>
              </div>
            </div>
          </template>

          <template #footer>
            <van-button @click="shan(item.id)" style="border:none" size="mini">  <span style="border:1px solid #EEF0F2;padding:3px;border-radius :25px;color:#7F7F80"> 删除订单</span></van-button>
         <van-button @click="zai" style="border:none" size="mini">  <span style="border:1px solid #2B92FA;padding:3px;border-radius :25px;color:#2B92FA"> 再来一单</span></van-button>
          </template>
        </van-card>
      </div>
    </div>

    <!-- 底部 -->
    <van-tabbar v-model="active">
      <van-tabbar-item
        :to="{
          name: 'shouye',
          params: { uId: uId, uName: uName },
        }"
        icon="home-o"
      >商家</van-tabbar-item>
      <van-tabbar-item
        :to="{
          name: 'sousuo',
          params: { uId: uId, uName: uName },
        }"
        icon="search"
      >搜索</van-tabbar-item>
      <van-tabbar-item
        :to="{
          name: 'dingdan',
          params: { uId: uId, uName: uName },
        }"
        icon="friends-o"
      >订单</van-tabbar-item>
      <van-tabbar-item
        :to="{
          name: 'wode',
          params: { uId: uId, uName: uName },
        }"
        icon="setting-o"
      >我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { Toast } from "vant";
import { Empty } from "vant";
// import { Empty } from 'vant';
import { Card } from "vant";
import { Tabbar, TabbarItem } from "vant";
export default {
  props: ["uId", "uName"],
  data() {
    return {
      active: 2,
      good: []
    };
  },
  created: function() {
    this.getGood();
  },
  methods: {
    // 删除订单
    shan:function(id){
var app=this;
this.$http.get("/biz/deleteOrderByOid?orderId="+id).then(function(res){
 app.getGood();
})
    },
    //遍历订单
    getGood:function(){
      var app=this;
        //  console.log(app.uId)
      this.$http.get("/biz/queryOrdersByUserId?userId="+app.uId).then(function(res){
        app.good=res.data;
           for (var i = 0; i < res.data.length; i++) {
              app.good[i].photo =
                "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/" +
              res.data[i].list[0].foodPhoto
            }
      })
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    denglu() {
      this.$router.push({ name: "denglu" });
    },
    zai(){
 this.$router.push({name:'shouye',params: {uId:this.uId, uName:this.uName }});
    },
  }
};
</script>

<style>
.bottom-button {
  width: 160px;
  height: 40px;
  background-color: #00a77c;
  border: #00a77c;
}
</style>



