    return {
      good: [],
      you: [], //全部优惠卷
      ping: [], //全部
      ping1: [], //好评
      ping2: [], //差评
      youa: "", //优惠卷数量
      youb: "", //第一个优惠卷
      fen: [], //左侧分类
      fenD: [], //左侧分类
      fenL: [], //右侧商品
      list:[],//购物车

      zongjia:0,
      active: 0,
      activeKey: 0,
      show: false
    };
  },
  created: function() {
    this.getGood();
    this.getYou();
    this.getPin();
    this.getFen();
    this.getFenl();
    // this.addshop();
  },
  methods: {
    // 提交订单
    onSubmit(){
      if(this.zongjia=='0'){
          Toast('未添加商品');
      }
      else{

         var app=this;
        app.$http
        .post("/biz/insertOrder", {
          userId: app.uId,
          shopId: app.shopid,
          list: app.list,
        }).then(function(res){
          console.log(res)
        })
      }
    Toast('提交成功');
      this.$router.push({name:'shouye',params: {uId:this.uId, uName:this.uName }});
    },
    //添加
addshop(id,uId,shopid){
// console.log(id);//商品id
// console.log(shopid)//商家id
// console.log(uId)//用户id
this.list.push({'foodId':id,"buyCount":1,"shopId":shopid,})
// console.log(this.list.length)
var sum=0;
  for(var i=0;i<this.list.length;i++){
    sum+=this.list[i].buyCount*18;
  }
  this.zongjia=sum;
  // if(sum==0){
  //   this.zongjia=0;
  // }else{
  //     this.zongjia=sum;
  // }

  console.log(sum)
  console.log(this.zongjia)
  // console.log(this.list)
},
// 商品分类
    getFenl: function(id) {
      // console.log(id);
      var app = this;
      if (id == undefined) {
        this.$http
          .get(
            "biz/queryFoodinfoByShopIdAndFoodCategoryId?shopId=" +
              app.shopid +
              "&foodcategoryId=" +
              1
          )
          .then(function(res) {
            app.fenD = res.data;
            console.log(res.data)
            for (var i = 0; i < res.data.length; i++) {
              app.fenD[i].photo =
                "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/" +
                res.data[i].photo;
            }
            // console.log(res.data)
          });
      } else {
        this.$http
          .get(
            "biz/queryFoodinfoByShopIdAndFoodCategoryId?shopId=" +
              app.shopid +
              "&foodcategoryId=" +
              id
          )
          .then(function(res) {
            // console.log(res.data)
            app.fenD = res.data;
            for (var i = 0; i < res.data.length; i++) {
              app.fenD[i].photo =
                "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/" +
                res.data[i].photo;
            }
            // console.log(res.data)
          });
      }
    },
    // 左边分类
    getFen: function() {
      var app = this;
      this.$http
        .get("/biz/queryFoodCategory?id=" + app.shopid)
        .then(function(res) {
          app.fen = res.data;
          // console.log(res.data)
        });
    },
    // 评论
    getPin: function() {
      var app = this;
      this.$http
        .get("/biz//queryCommentByShopId?shopId=" + app.shopid)
        .then(function(res) {
          app.ping = res.data;
          //  console.log(res.data)
          //  console.log(res.data[2].isGood);
          for (var i = 0; i < res.data.length; i++) {
            app.ping[i].score = app.ping[i].score * 0.1;
            app.ping[i].account =
              app.ping[i].account[0] + "*******" + app.ping[i].account[10];
            // console.log(app.ping[i].account);
            if (res.data[i].isGood == "0") {
              app.ping1.push(res.data[i]);
            } else {
              app.ping2.push(res.data[i]);
            }
            // console.log(res.data[i].isGood)
            // console.log(app.ping1)
            // console.log(app.ping2)
          }
        });
    },
    // 优惠卷
    getYou: function() {
      var app = this;
      this.$http
        .get("/biz//querySpecialOfferByShopId?shopId=" + app.shopid)
        .then(function(res) {
          app.you = res.data;
          app.youa = res.data.length;
          app.youb = res.data[0];
          // console.log(res.data);
          // console.log(res.data[0]);
          // console.log(res.data.length);
        });
    },
    // 商家
    getGood: function() {
      var app = this;
      this.$http
        .get("/biz//queryInfoByShopId?shopId=" + app.shopid)
        .then(function(res) {
          console.log(res.data);
          app.good = res.data;
          app.good.photo =
            "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/" +
            res.data.photo;
        });
    },
    showPopup() {
      this.show = true;
    },
    onClickLeft() {
      this.$router.push({name:'shouye',params: {uId:this.uId, uName:this.uName }});
    }
  }
};
</script>

<style>
#s-a1 {
  width: 100px;
  margin: 0 auto;
  z-index: 1;
  top: -20px;
  position: relative;
}
#s-a2 {
  background-color: #ffe434;
  padding: 3px;
  font-weight: 600;
  font-size: 10px;
}
#s-a3 {
  font-size: 23px;
  font-weight: 600;
}
/* 特价 */
.s-b1 {
  background-color: #e38d5b;
  padding: 2px;
  font-size: 5px;
  color: #fff;
}
/* 首单 */
.s-b1-1 {
  background-color: #82b957;
  font-size: 5px;
  padding: 2px;
  color: #fff;
}
/* 减免 */
.s-b1-2 {
  background-color: #e17a77;
  font-size: 5px;
  padding: 2px;
  color: #fff;
}
#s-b2 {
  margin-left: 5px;
  font-size: 13px;
}
#s-b3 {
  background-color: #ffe434;
  font-size: 15px;
  color: #fff;
}
#s-b4 {
  font-size: 25px;
  font-weight: 600;
  color: #000;
  text-align: center;
}

.s-b6 {
  margin-top: 5px;
  width: 100%;
  margin-left: 10%;
  text-align: center;
}
.s-b6-1 {
  color: #000;
}
.s-c1 {
  background: #7232dd;
  color: #fff;
  font-size: 10px;
  padding: 2px;
  border: 1px solid #7232dd;
  border-radius: 10%;
}
#s-c2 {
  /* position: relative;
  top: 10px; */
  margin-left: 2px;
  font-size: 13px;
  color: #a1a6a8;
}
.s-c3 {
  padding: 20px;
  border: 1px solid #f7f7f8;
  background: #fff;
}
#s-c3-1 {
  border-right: 1px solid rgb(220, 221, 222);
  text-align: center;
  font-size: 10px;
  position: relative;
  top: 15px;
}
#s-c3-2 {
  text-align: center;
  position: relative;
  top: 15px;
}
.s-c4 {
  font-weight: 600;
  font-size: 15px;
}
.s-c5 {
  padding: 10px;
  background: #dcddde;
  color: #000;
}
.s-c5-1 {
  padding: 10px;
  background: #4aa478;
  color: #fff;
}
.s-c6 {
  margin-top: 5%;
  background: #fff;
}

#s-d1 {
  background: #f4f5f7;
  color: #94999f;
  min-height: 40px;
}
</style>



